import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Commitment from "../views/Commitment.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  /* {
    path: "/objective/",
    name: "Objective",
    component: Home
  }, */
  {
    path: "/commitment",
    name: "Commitment",
    component: Commitment,
    props: true
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
