        <div class="col-xl-5 col-5 col-lg-5 wrapper-desktop">
          <div> <img :src="require(`@/assets/images/page2-img1.jpg`)" class="com-gallery"> </div>
        </div>
      </div>
    </div>

    <div class="com-container mx-auto mt-5">
      <div class="row com-component">
        <div class="col-xl-5 col-5 col-lg-5  "> <img :src="require(`@/assets/images/page2-img2.jpg`)" class=" float-right com-gallery"> </div>
        <div class="col-xl-7 col-7 col-lg-7 ">
          <div class="texr-wrapp"> How often are you able to use our app to refresh what you have
            learned for 5-10 minutes? </div>
          <div class="RangeSlider" id="RangeSlider2">
            <div class="RangeSlider_ParticipantName2"></div>
            <div class="RangeSlider_ClickArea2">
              <div class="data-slider-wrapper">
                <div class=vector data-labels='["0x","1X","2X","3X","4x", "5x"]'>

                  <div class=vector-up>
                    <output class=output name=output for=vector><span></span></output>
