    <div class="com-container mx-auto">
      <div class="com-component justify-content-center text-center" style="margin-top: 8%">
        <!-- <input
          class="submit-btn btn btn-success"
          id="submit_info"
          type="button"
          value="SUBMIT"
        /> -->
        <button class="submit-btn btn btn-success" id="submit_info" @click="submit">SUBMIT</button>
        <br />
        <a href="/home">Back to Dashboard>></a> </div>
    </div>
