</template>


<script>
  import axios from 'axios';

  export default {
    props:['carouselBoxes'],
    data() {
      return{
        learningObject:'',
        topRangeSlider: null,
        bottomRangeSlider: null
      }
    },
    mounted () {
      this.topRangeSlider = new window.TopRangeSlider('RangeSlider');
      this.bottomRangeSlider = new window.BottomRangeSlider('RangeSlider2');
    },
    methods:{
      async submit() {

        let token = localStorage.getItem("chinesepod_token");

        if(!token) {
          this.$alert("Something went wrong, please try again!", 'Oops!', 'error');
          setTimeout(() => {
            window.location = '/objectives'
          }, 1000)
        }

        let promises = [];

        if (this.carouselBoxes && this.carouselBoxes.length) {
          this.learningObject = [];
          for (let i = 0; i < this.carouselBoxes.length; i++) {
            if (this.carouselBoxes[i].isSelected) {
              this.learningObject.push(this.carouselBoxes[i].text)
            }
          }
          promises.push(
            axios.put('https://www.chinesepod.com/api/v1/account/settings/update',
              { //CORS: '*'
                type: "learningObjective",
                value: JSON.stringify(this.learningObject)
              },
              {
                headers: {
                  Authorization: 'Bearer ' + token
                }
              }
            )
          )
        }

        promises.push(
          axios.put('https://www.chinesepod.com/api/v1/account/settings/update',
            { //CORS: '*'
              type: "studyCommitment",
              value: this.topRangeSlider.getSliderValue()
            },
            {
              headers: {
                Authorization: 'Bearer ' + token
              }
            }
          )
        )
        promises.push(
          axios.put('https://www.chinesepod.com/api/v1/account/settings/update',
            { //CORS: '*'
              type: "reviewCommitment",
              value: this.bottomRangeSlider.getSliderValue()
            },
            {
              headers: {
                Authorization: 'Bearer ' + token
              }
            }
          )
        )
        await Promise.all(promises)
          .then(() => {
            this.$alert("Your answers have been saved!", 'Great!', 'success');
            setTimeout(() => {
              window.location = '/home'
            }, 2000)
          })
          .catch(() => {
            this.$alert("Something went wrong, please try again!", 'Oops!', 'error');
          })

      }
    }
  };

</script>
