<template>
  <div class="home" v-show="bShow">
    <Header />
    <Hero />
    <Home-carousel />
    <Footer />
  </div>
</template>


<script>
import axios from 'axios';
import Hero from "@/components/Hero";
import HomeCarousel from "@/components/HomeCarousel";
import Header from "@/components/Header";
import Footer from "@/components/Footer";


export default {
  name: "Home",
  data() {
    return {
      bShow: true
    }
  },
  components: {
    Header,
    Hero,
    HomeCarousel,
    Footer
  },
  mounted () {
    axios.post('https://www.chinesepod.com/api/v1/token/auth',
    {
      key: "XcvgbyVVcbNA8AZ155XM68CjE4WawyikUfACEdvF",
      type: "objective"
    }
    ).then(response => {
      if (response.data.token) {
        this.bShow = true;
        localStorage.setItem("chinesepod_token", response.data.token);
      } else {
        window.location = "/";
      }
    }).catch(err => {
      if (err) {
        window.location = "/";
      }
    });
  }
};
</script>
